import React, { Fragment } from "react";
import "react-datepicker/dist/react-datepicker.css";
import DatePicker from "react-datepicker";
// import '../validations'
import '../DARIS.css';
import { request } from "../helper";
import { withRouter } from "react-router-dom";

class AddEmployee extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      lastdate: '',
      userdate: '',
      employee_id: '',
      title: '',
      firstname: '',
      lastname: '',
      email: '',
      password: '',
      mobile: '',
      gender: '',
      designation: '',
      designations: [],
      location: '',
      branch: '',
      state : '',
      region: '',
      role: '',
      locations: [],
      branches: [],
      higher_position: ''
    }
  }

  componentDidMount() {
    console.log(this.props.history)
    if(sessionStorage.length === 0){return;}
    
    (async () => {
      const token = sessionStorage.getItem('token');
      let desg = await request('designation/all', token).get();
      let json = await desg.json();

      let bran = await request("branch/all", token).get();
      let resp = await bran.json();

      const locationsArray = [];

      resp.message.map( location => {
          if(!locationsArray.includes(location.location.trim())){
              locationsArray.push(location.location);
          }
      })

      this.setState({
        designations: json.message,
        branches : resp.message,
        locations: locationsArray,
      })
    })();
  }

  // DatePicker
  Changedate = (e) => { this.setState({ lastdate: e, }); };
  ChangeUserdate = (e) => { this.setState({ userdate: e, }); };

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  check = () => {
    if (isNaN(Number(this.state.employee_id))) {
      alert("Please Enter valid Employee Id");
      return false;
    } else if (isNaN(Number(this.state.mobile))) {
      alert("Please Enter valid Mobile Number");
      return false;
    }

    return true;
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      employee_id: Number(this.state.employee_id),
      title: this.state.title,
      firstname: this.state.firstname,
      lastname: this.state.lastname,
      email: this.state.email,
      password: this.state.password,
      mobile: Number(this.state.mobile),
      gender: this.state.gender,
      designation: this.state.designation,
      location: this.state.location,
      branch: this.state.branch,
      state: this.state.state,
      region: this.state.region,
      role: this.state.role,
      higher_position : Number(this.state.higher_position)
    }

    if (this.check() === false) return;
    const token = sessionStorage.getItem('token');
    let res = await request("employee/add", token).post(data);
    // let json = await res.json();

    if(res.status === 200){
      alert("Employee Added Succesfully")
    }
    
    Array.from(document.querySelectorAll("input")).forEach(
      input => (input.value = "")
    );

    this.setState({
      itemvalues: [{}]
    });

    this.props.history.push({ pathname: `/Home` })
  };


  renderHigherPositionInput = (SelectedRole) =>{
      return(
                <Fragment>
                  <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    <label htmlFor="exampleInputPassword1" >{SelectedRole} ID</label>
                      <input type="text" onChange={this.handleChange} name="higher_position" className="form-control inputField" placeholder={`${SelectedRole} ID`} autoComplete="off" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  </div>
                </Fragment>
      )
  }

  higherPosition = ()=>{
    switch (this.state.role) {
      case "Telecaller":
        return(
               this.renderHigherPositionInput("Team Lead")
              )
      case "Zonal Manager":
        return(
          this.renderHigherPositionInput("RSM")
              )
      case "Branch Manager":
        return(
          this.renderHigherPositionInput("Zonal Manager")
              ) 
      case "Senior Relationship Manager":
      case "Telesales Manager":
      case "Assistant Manager":
        return(
          this.renderHigherPositionInput("Branch Manager")
              )
      case "Relationship Manager":
        return(
          this.renderHigherPositionInput("Senior Relationship Manager")
              )
      case "Senior Team Lead":
        return(
          this.renderHigherPositionInput("Telesales Manager")
              )
      case "Team Lead":
      case "PTL":
      case "ITL":
      case "HCL":
        return(
          this.renderHigherPositionInput("Senior Team Lead")
              )
      default:
        break;
    }
  }

  render() {
    return (
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Employee Details</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit}>
              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label >Employee ID <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <input type="text" placeholder="Employee ID" name="employee_id" autoComplete="off"
                    onChange={this.handleChange} className="form-control inputField" required />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 d-flex" style={{ display: "block" }}>
                  <div className="col-sm-4 pr-0 pl-0">
                    <label htmlFor="exampleInputPassword1" >Title <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                    <select required className="form-control inputField" name="title" onChange={this.handleChange}>Title
                      <option value="">Title</option>
                      <option>Mr</option>
                      <option>Miss</option>
                      <option>Mrs</option>
                    </select>
                  </div>

                  <div className="col-sm-9 pl-0">
                    <label htmlFor="exampleInputPassword1" >First Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                    <input type="text" onChange={this.handleChange} name="firstname" required className="form-control inputField" placeholder="First Name" autoComplete="off" />
                  </div>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" >Last Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <input type="text" onChange={this.handleChange} name="lastname" required className="form-control inputField" placeholder="Last Name" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" >Mobile Number <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <input type="text" onChange={this.handleChange} name="mobile" required className="form-control inputField" aria-describedby="emailHelp" placeholder="Mobile Number" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" >Email <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <input type="email" onChange={this.handleChange} name="email" required className="form-control inputField" placeholder="Email" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1" >Password <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <input type="password" onChange={this.handleChange} name="password" required className="form-control inputField" placeholder="Password" autoComplete="off" />
                </ div>
              </div>

              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Gender <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <select required className="form-control inputField" onChange={this.handleChange} name="gender">
                    <option value="">Select Gender</option>
                    <option>Male</option>
                    <option>Female</option>
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 " >
                  <label htmlFor="exampleInputPassword1" >Last Updated Date</label>
                  <div className="form-control inputField">
                      <DatePicker required className="dateField" selected={this.state.lastdate} placeholderText="Select Date" showPopperArrow={false}
                      onChange={this.Changedate} onKeyDown={(e) => e.preventDefault()} />
                  </div>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" >
                  <label htmlFor="exampleInputPassword1">User Updated Date</label>
                  <div className="form-control inputField">
                    <DatePicker required className="dateField" selected={this.state.userdate} placeholderText="Select Date" showPopperArrow={false}
                      onChange={this.ChangeUserdate} onKeyDown={(e) => e.preventDefault()} />
                  </div>
                </div>
              </div>

              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Designation <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <select required className="form-control inputField" onChange={this.handleChange} name="designation" >
                    <option value="">Select Designation</option>
                    {
                      this.state.designations.map((item, index) => {
                        return (
                          <option key={item.designation + index} value={item.designation}>
                            {item.designation}
                          </option>
                        )
                      })
                    }
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Location <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <select required className="form-control inputField" id="location" onChange={this.handleChange} name="location" >
                    <option value="">Select Location</option>
                    {
                      this.state.locations.map((item, index) => {
                        return (
                          <option key={index} value={item}>
                            {item}
                          </option>
                        )
                      })
                    }
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Branch <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <select required className="form-control inputField" onChange={this.handleChange} name="branch" >
                    <option value="">Select Branch Name</option>
                    {
                      this.state.branches.map((item, index) => {
                        if(item.location === this.state.location){
                          return (
                            <option key={item.branch + index} value={item.branch}>
                              {item.branch}
                            </option>
                          )
                        }
                      })
                    }
                  </select>
                </div>
              </div>

              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">State <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <select required className="form-control inputField" id="StateName" onChange={this.handleChange} name="state" >
                    <option value="">Select State Name</option>
                    <option>Andhra Pradesh</option>
                    <option>Kerala</option>
                    <option>Karnataka</option>
                    <option>Telangana</option>
                    <option>Tamilnadu</option>
                    <option>New Delhi</option>
                    <option>Haryana</option>
                    <option>Jammu and Kashmir</option>
                    <option>Uttar Pradesh</option>
                    <option>Punjab</option>
                    <option>Uttarakhand</option>
                    <option>Goa</option>
                    <option>Maharashtra</option>
                    <option>Rajasthan</option>
                    <option>Gujarat</option>
                    <option>Bihar</option>
                    <option>Orissa</option>
                    <option>Jharkhand</option>
                    <option>West Bengal</option>
                    <option>Chattisgarh</option>
                    <option>Madhya Pradesh</option>
                    <option>Himachal Pradesh</option>
                    <option>Puducherry</option>
                    <option>Assam</option>
                    <option>Arunachal Pradesh</option>
                    <option>Mizoram</option>
                    <option>Meghalaya</option>
                    <option>Manipur</option>
                    <option>Nagaland</option>
                    <option>Sikkim</option>
                    <option>Tripura</option>
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label id='region'>Region <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <select required className="form-control inputField" id="region" onChange={this.handleChange} name="region">
                    <option value="">Select Region</option>
                    <option value="South">South</option>
                    <option value="East">East</option>
                    <option value="West">West</option>
                    <option value="North">North</option>
                    <option value="Central">Central</option>
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label id="role">Role Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                  <select required className="form-control inputField" id="role" onChange={this.handleChange} name="role" >
                    <option value="">Select Role Name</option>
                    <option value="NSM">NSM</option>
                    <option value="RSM">RSM</option>
                    <option value="Zonal Manager">Zonal Manager</option>
                    <option value="Branch Manager">Branch Manager</option>
                    <option value="Assistant Manager">Assistant Manager</option>
                    <option value="Telesales Manager">Telesales Manager</option>
                    <option value="Senior Relationship Manager">Senior Relationship Manager</option>
                    <option value="Relationship Manager">Relationship Manager</option>
                    <option value="Senior Team Lead">Senior Team Lead</option>
                    <option value="Team Lead">Team Lead</option>
                    <option value="Telecaller">Telecaller</option>
                  </select>
                </div>
              </div>
              {this.higherPosition()}
              <div className="d-flex" style={{ width: "100%" }}>
                <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)",marginTop:"50px", marginLeft:"15px", color: "white", border: "none" }}>Submit</button>
              </div>
            </form>
          </div>
       
        </section>
      </Fragment>
    );
  }
}

export default withRouter(AddEmployee);