import React, { Fragment } from 'react';
import { request, dateFormat } from '../helper';
import { css } from "@emotion/core";
import BounceLoader from "react-spinners/BounceLoader";

export default class MutualFunds extends React.Component {
  constructor (props) {
    super(props)
    this.state = { branch: '', location: '', getData: [] }
  }

  async componentDidMount() {
    if(sessionStorage.length === 0){return;}
    const token = sessionStorage.getItem('token');
    let res = await request('branch/all', token).get();
    let json = await res.json();
    this.setState({
      getData: json.message
    });
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      location: this.capitalizeFirstLetter(this.state.location.trim()),
      branch: this.capitalizeFirstLetter(this.state.branch.trim()),
    }
    const token = sessionStorage.getItem('token');
    let res = await request("branch/add", token).post(data);

    let response = await res.json();

    if (response.status === 200) {
      this.setState({
        getData: response.message
      });
      alert("Branch added successfully");
    }
    else if (response.status === 302) {
      alert("Branch Already Existed");
    }
    Array.from(document.querySelectorAll("input")).forEach(
      input => (input.value = "")
    );
    this.setState({
      itemvalues: [{}]
    });
  };

  delete = async (id) => {
    if (window.confirm("Delete the Branch ?")) {
      const token = sessionStorage.getItem('token');
      let response = await request(`branch/delete/${id}`, token).get();
      let getData = await response.json();
      this.setState({ getData: getData.message
       });
    }
  }
  
  render() {
    
    return (
      <Fragment>
        <section className="mx-auto col-sm-7 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-11 mb-3 heading p-2" style={{borderRadius:"5px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}>Add Location and Branch</h4>
          <div className="headTwo pt-4" style={{borderRadius:"5px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}>
            <form onSubmit={this.handleSubmit}>
              <div className="form-group pt-0 pb-0 pr-3 pl-3" >
                <label htmlFor="exampleInputEmail1" className="mb-0">Add Location </label>
                <input type="text" onChange={this.handleChange} name="location" required className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Location" autoComplete="off" style={{display:"block", backgroundColor: "White", color: "#495057", lineHeight: "1.5", fontSize: "1rem", borderRadius:"5px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}/>
              </div>
              <div className="form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputPassword1" className="mb-0">Add Branch </label>
                <div >
                <input type="text" onChange={this.handleChange} name="branch" required className="form-control" id="exampleInputPassword1" placeholder="Branch" autoComplete="off" style={{display:"block", backgroundColor: "White", color: "#495057", lineHeight: "1.5", fontSize: "1rem", borderRadius:"5px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}/>
                </div>
              </div>
              <div className="d-flex" style={{ width: "100%" }}>
                <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "5px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} />
              </div>
            </form>
          </div>
        </section> 

        <h4 style={{textAlign:"center", margin: '10px auto'}}>Loaction and Branch Details</h4>
        {
          this.state.getData.length !== 0 ?
          <table className="table table-bordered" >
          <thead>
            <tr style={{textAlign:"left", backgroundColor: "rgb(219, 144, 31)" }}>
              <th scope="col">S.No</th>
              <th scope="col">Location</th>
              <th scope="col">Branch</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>
            {
              (this.state.getData !== null || this.state.getData !== undefined)
                ? (this.state.getData.map((data, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{data.location}</small></td>
                      <td><small>{data.branch}</small></td>
                      {/* <td><small onClick={() => this.delete(data.id)}>Delete</small></td> */}
                      <td><small onClick={() => this.delete(data.id)}><i className="far fa-trash-alt"></i></small></td>
                    </tr>
                  )
                }))
                : null
            }
          </tbody>
        </table> :
        <div style={{width: '100%', margin : '50px auto'}}> 
         <BounceLoader css={css`display: block; margin: 0 auto; border-color: red;`} size={50} />
        </div>
        }
      </Fragment>
    );
  }
}