import React, { Fragment, Component } from 'react';
import { request, dateFormat } from '../helper';
import { css } from "@emotion/core";
import BounceLoader from "react-spinners/BounceLoader";

export default class MutualFunds extends Component {
  constructor (props) {
    super(props)
    this.state = { designation: '', getData: null }
  }
  
  async componentDidMount() {
    if(sessionStorage.length === 0){return;}
    const token = sessionStorage.getItem('token');
    let res = await request('designation/all', token).get();
    this.setState({
      getData: await res.json()
    });
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }


  handleSubmit = async (event) => {
    event.preventDefault();
    const data = { designation: this.state.designation }
    const token = sessionStorage.getItem('token');
    let res = await request("designation/add", token).post(data);

    let response = await res.json();
    if (response.status === 200) {
      this.setState({
        getData: response
      });
      alert("Designation added successfully");
    }
    else if (response.status === 302) {
      alert("Designation Already Existed");
    }

    Array.from(document.querySelectorAll("input")).forEach(
      input => (input.value = "")
    );
    this.setState({
      itemvalues: [{}]
    });
  };

  delete = async (id) => {
    if (window.confirm("Delete the Designation ?")) {
      const token = sessionStorage.getItem('token');
      let response = await request(`designation/delete/${id}`, token).get();
      let getData = await response.json();
      this.setState({ getData: getData });
    }
  }

  render() {
    const { getData } = this.state;
    return (
      <Fragment>
        <section className="mx-auto col-sm-7 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-11 mb-3 heading p-2">Add Designation</h4>
          <div className="headTwo pt-4" style={{borderRadius:"5px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}>
            <form onSubmit={this.handleSubmit}>
              <div className="form-group pt-0 pb-0 pr-3 pl-3" >
                <label htmlFor="exampleInputEmail1" className="mb-0">Designation Name</label>
                <input type="text" onChange={this.handleChange} name="designation" required className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Designation Name" autoComplete="off" />
              </div>
              <div className="d-flex" style={{ width: "100%" }}>
                 <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} />
              </div>
            </form>
          </div>
        </section>

        <h5 style = {{textAlign: "center", margin : "15px auto"}}>Designation</h5>
        {
          getData ? 
          
          <table className="table table-bordered">
          <thead style={{backgroundColor:"rgb(219, 144, 31)", color:"white", textAlign: "left"}}>
            <tr>
              <th scope="col">S.No</th>
              <th scope="col">Designation</th>
              <th scope="col">Delete</th>
            </tr>
          </thead>
          <tbody>

            {
              (getData !== null)
                ? (getData.message.map((dataa, i) => {
                  return (
                    <tr key={i} >
                      <th scope="row">{i + 1}</th>
                      <td><small>{dataa.designation}</small></td>
                      <td><small onClick={() => this.delete(dataa.id)}>Delete</small></td>
                    </tr>
                  )
                }))
                : null
            }
          </tbody>
        </table> : 
        <div style={{width: '100%', margin : '50px auto'}}> 
        <BounceLoader css={css`display: block; margin: 0 auto; border-color: red;`} size={50} />
       </div>
        }  
      </Fragment>
    );
  }
}