import React, { Fragment, useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import { useSpring, animated } from 'react-spring'
import "react-datepicker/dist/react-datepicker.css";
import { request, dateFormat } from '../helper';
import './Reports.css';
import Button from "@material-ui/core/Button";
import { css } from "@emotion/core";
import BounceLoader from "react-spinners/BounceLoader";

const Appointemnts = () => {
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [selectedStart, setSelectedStart] = useState('');
  const [selectedEnd, setSelectedEnd] = useState('');
  const [TcId, setTcId] = useState("");
  const [employeeIds, setEmployeeIds] = useState([]);
  const [data, setData] = useState([]);
  const [TeamWise, setTeamWise] = useState([]);
  const [TSManager, setTSManager] = useState('');
  const [SeniorTL, setSeniorTL] = useState('');
  const [TeamLead, setTeamLead] = useState('');
  const [SearchBar, setSearchBar] = useState('All Employees');
  const [EmployeeData, setEmployeeData] = useState('');


  useEffect(async () => {
    // const employeeId = sessionStorage.getItem('_id');
    if(sessionStorage.length === 0){return;}
    const token = sessionStorage.getItem('token');
    const res = await request(`employee/role`, token).get();
    const json = await res.json();
    let response = await request('employee/select', token).get();
    let data = await response.json();

    let employeeResponse = await request(`employee/select/employeeDetails`, token).get();
    let employeeData = await employeeResponse.json();
    setEmployeeData(employeeData.message[0])
    setTeamWise(data.message); 
    setEmployeeIds(json);
    },[])

  let reset = () => {
    setStartDate('');
    setEndDate('');
  }

  let searchChange = (e) => {
    setSearchBar(e.target.name)
  }

  let TcIdChange = (e) => {
    setTcId(e);
  }

  let startDateChange = (e) => {
    setStartDate(e);
    setSelectedStart(dateFormat(new Date(e).toLocaleDateString()));
  }

  let endDateChange = (e) => {
    setEndDate(e);
    setSelectedEnd(dateFormat(new Date(e).toLocaleDateString()));
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const EmployeeArray = employeeIds.map( employee => employee.employee_id);
    
    if(!EmployeeArray.includes(Number(e.target[0].value))){
      alert("Please enter valid employee id");
      return;
    }
    const token = sessionStorage.getItem('token');
    var requestObj = { start_date: selectedStart, end_date: selectedEnd, id: TcId };
    let res = await request('reports/closers', token).post(requestObj);
    let json = await res.json();
    if (json == '') { alert('No Results are Found'); }

    setData(json);
    reset();
  }
  const props = useSpring({
    opacity: 1,
    from: { opacity: 0 }
  })


  let buttonsFunction = () => {
    if(["CEO", "RSM", "NSM"].includes(EmployeeData.role)){
      return (
        <div className="employeesBox">
          <div className="teamWise">
            <button className="searchBarButton" name='Team Wise' onClick={(e) => searchChange(e)}>Team Wise</button>
          </div>
          <div className="allEmployees">
            <button className="searchBarButton" name='All Employees' onClick={(e) => searchChange(e)}>All Employees</button>
          </div>
        </div>
      )
    } 
  }



  let searchBarFunction = () => {
     switch (SearchBar) {
       case 'All Employees':  
            return (<form onSubmit={handleSubmit}>
              {
                employeeIds.length !== 0 ?
                <div className="d-flex row justify-content-between mt-3">
                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    <label htmlFor="exampleInputEmail1" className="mb-0">Employee ID</label>
                    <input style={{width:"205px", height:"38px", marginLeft:"10px"}} list="suggestions" placeholder="Select Employee ID" onChange={(e)=>{TcIdChange(e.target.value)}}/>
                    <datalist id="suggestions">
                          {
                              employeeIds.map((employee, i)=>{
                                return(<option key={i} value={employee.employee_id}>{employee.firstname} | {employee.designation} | {employee.branch} </option>)
                            })
                          }
                          {console.log(employeeIds.length !== 0 ? true : false)}
                    </datalist>
                  </div>
                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    <label htmlFor="exampleInputPassword1" className="mb-0" style={{marginRight: "22px"}}>From Date</label>
                    <DatePicker className="form-control" style={{ zIndex : '1500'}} selected={startDate} onChange={date => startDateChange(date)} required placeholderText="Select Date" />
                  </div>
                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    <label htmlFor="exampleInputPassword1" className="mb-0" style={{marginRight: "40px"}}>To Date</label>
                    <DatePicker className="form-control" style={{zIndex : '1500'}} minDate={startDate} selected={endDate} onChange={date => endDateChange(date)} required placeholderText="Select Date" />
                  </div>
                </div> : 
                <div style={{padding : '25px'}}>
                  <BounceLoader css={css`display: block; margin: 0 auto; border-color: red;`} size={50} />
                </div>
              }
              <div className="d-flex" style={{ width: "100%" }}>
                <Button className="mx-auto" type="submit" variant="contained" color="primary">Submit</Button>
              </div>
            </form>)
       case 'Team Wise':
         return (
          <form onSubmit={handleSubmit}>
          <div className="d-flex row justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">TS Manager</label>
                <select style={{width:"205px", height:"38px", marginLeft:"10px"}}  onChange={(e)=>{setTSManager(e.target.value); TcIdChange(e.target.value)}}>
                  <option>Select</option>
                    {
                      TeamWise.map((employee, index) => {
                        if(employee.role === "Telesales Manager"){
                          return (<option key={index} value={employee.employee_id}>{employee.firstname} {employee.employee_id}</option> )
                        }
                      })
                    }
                </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Senior TL &nbsp;&nbsp;&nbsp; </label>
                <select style={{width:"205px", height:"38px", marginLeft:"10px"}}  onChange={(e)=>{setSeniorTL(e.target.value); TcIdChange(e.target.value)}}>
                    <option>Select</option>
                    {
                      TeamWise.map((employee, index) => {
                        if(employee.role === "Senior Team Lead" && employee.higher_position === Number(TSManager)){
                          return (<option key={index} value={employee.employee_id}>{employee.firstname} {employee.employee_id}</option> )
                        }
                      })
                    }
                </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                <label htmlFor="exampleInputEmail1" className="mb-0">Team Lead &nbsp;&nbsp;</label>
                <select style={{width:"205px", height:"38px", marginLeft:"10px"}}  onChange={(e)=>{setTeamLead(e.target.value); TcIdChange(e.target.value)}}>
                    <option>Select</option>
                    {
                      TeamWise.map((employee, index) => {
                        if(employee.role === "Team Lead" && employee.higher_position === Number(SeniorTL)){
                          return (<option key={index} value={employee.employee_id}>{employee.firstname} {employee.employee_id}</option> )
                        }
                      })
                    }
                </select>
            </div>
          </div>
          <div className="d-flex row justify-content-between mt-3">
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1" className="mb-0">Telecaller &nbsp;&nbsp;&nbsp;</label>
                  <select style={{width:"205px", height:"38px", marginLeft:"10px"}}  onChange={(e)=>{TcIdChange(e.target.value)}}>
                  <option>Select</option>
                  {
                      TeamWise.map((employee, index) => {
                        if(employee.role === "Telecaller" && employee.higher_position === Number(TeamLead)){
                          return (<option key={index} value={employee.employee_id}>{employee.firstname} {employee.employee_id}</option> )
                        }
                      })  
                    }
                  </select>
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0" style={{marginRight: "22px"}}>From Date</label>
              <DatePicker className="form-control" style={{ zIndex : '1500'}} selected={startDate} onChange={date => startDateChange(date)} required placeholderText="Select Date" />
            </div>
            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
              <label htmlFor="exampleInputPassword1" className="mb-0" style={{marginRight: "40px"}}>To Date</label>
              <DatePicker className="form-control" style={{zIndex : '1500'}} minDate={startDate} selected={endDate} onChange={date => endDateChange(date)} required placeholderText="Select Date" />
            </div>
          </div>
          <div className="d-flex" style={{ width: "100%" }}>
            <Button className="mx-auto" type="submit" variant="contained" color="primary">Submit</Button>
          </div>
        </form> 
         )
     }
  }


  return (
    <animated.div style={props}>
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Closer Reports</h4>
          <div className="employees">
              {
               buttonsFunction()
             }
          </div>
          <div className="headTwo pt-4">
              {
                searchBarFunction()
              }
          </div>
        </section>
         
                  
        <div style={{ overflowX: "scroll"}}>
          <table className="table table-bordered">
            <thead>
              <tr>
                <th scope="col">S.No</th>
                <th scope="col">Customer Name</th>
                <th scope="col">Product Name</th>
                <th scope="col">Mobile Number</th>
                <th scope="col">Email ID</th>
                <th scope="col">Location</th>
                <th scope="col">TSO ID</th>
                <th scope="col">TSO Name</th>
                <th scope="col">Branch</th>
              </tr>
            </thead>
            <tbody>
              {
                data.map((item, index) => {
                  return (
                    <tr key={index}>
                      <th scope="row">{index + 1}</th>
                      <td><small>{item.first_name} {item.last_name}</small></td>
                      <td><small>{item.product}</small></td>
                      <td><small>{item.mobile}</small></td>
                      <td><small>{item.email}</small></td>
                      <td><small>{item.location}</small></td>
                      <td><small>{item.employee_id}</small></td>
                      <td><small>{item.firstname}</small></td>
                      <td><small>{item.branch}</small></td>
                    </tr>
                  )
                })
              }
            </tbody>
          </table>
        </div>
      </Fragment>
    </animated.div>
  );
};


export default Appointemnts;