import React, { Fragment, useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import {useSpring, animated} from 'react-spring';
import './branches.css';
import "react-datepicker/dist/react-datepicker.css";
import { request, dateFormat } from '../helper';
import Button from "@material-ui/core/Button";
import { css } from "@emotion/core";
import BounceLoader from "react-spinners/BounceLoader";

const  Rejects = () => {
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [selectedStart, setSelectedStart] = useState('');
  const [selectedEnd, setSelectedEnd] = useState('');
  const [selectedLocation, setSelectedLocation] = useState('');
  const [selectedBranch, setSelectedBranch] = useState('');
  const [Branches, setBranches] = useState([]);
  const [Locations, setLocations] = useState([]);
  const [data, setData] = useState([]);
  const [EmployeeData, setEmployeeData] = useState('');

  useEffect(async () => {
    if(sessionStorage.length === 0){return;}

    try {
      const token = sessionStorage.getItem('token');
      let branches = await request("branch/all", token).get();
      const json = await branches.json();
      const data = await request(`employee/select/employeeDetails`, token).get();
      const EmployeeDetails = await data.json();
      setEmployeeData(EmployeeDetails.message[0])
      if(EmployeeDetails.message[0].role === 'Branch Manager'){
            setSelectedLocation(EmployeeDetails.message[0].location);                
            setSelectedBranch(EmployeeDetails.message[0].branch);
      }

      const LocationsArray = [];
      for(const location of json.message){
          if(!LocationsArray.includes(location.location)){
              LocationsArray.push(location.location);
          }
      }
      setBranches(json.message)
      setLocations(LocationsArray);
    } catch (error) {
      alert(error);
    }
  }, [])

  let reset = () => {
    setStartDate('');
    setEndDate('');
  }


    let startDateChange = (e) => {
      setStartDate(e);
      setSelectedStart(dateFormat(new Date(e).toLocaleDateString()));
    }
  
    let endDateChange = (e) => {
      setEndDate(e);
      setSelectedEnd(dateFormat(new Date(e).toLocaleDateString()));
    }

    const branchSelect = () =>{

      switch (EmployeeData.role) {
        case 'CEO':
          return(<div className="d-flex row justify-content-between mt-3">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 FieldDiv">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Location</label>
                      <div className="SelectDiv">
                        <select  onChange={(e) => setSelectedLocation(e.target.value)} className='form-control SelectField'>
                          <option value='' >Select Location</option>
                              {
                                  Locations.map((item, i)=>{
                                      return (
                                          <option key={i} value={item}>{item}</option>
                                      )
                                  })
                              }
                        </select>
                      </div>
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 FieldDiv">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Branch</label>
                      <div className="SelectDiv">
                        <select onChange={(e) => setSelectedBranch(e.target.value)}  className='form-control SelectField'>
                        <option value=''>Select Branch</option>
                              {
                                  Branches.map((item, i)=>{
                                    if(item.location === selectedLocation){
                                      return (
                                          <option key={i} value={item.branch}>{item.branch}</option>
                                      )
                                    }
                                  })
                              }
                      </select>
                      </div>
                    </div>
                  </div>)
        case 'Zonal Manager':
          return(<div className="d-flex row justify-content-between mt-3">
          <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 FieldDiv">
            <label htmlFor="exampleInputEmail1" className="mb-0">Location</label>
            <div className="SelectDiv">
              <select  onChange={(e) => setSelectedLocation(e.target.value)} className='form-control SelectField'>
                      <option value={EmployeeData.location}>{EmployeeData.location}</option>
              </select>
            </div>
          </div>
          <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 FieldDiv">
            <label htmlFor="exampleInputEmail1" className="mb-0">Branch</label>
            <div className="SelectDiv">
              <select onChange={(e) => setSelectedBranch(e.target.value)}  className='form-control SelectField'>
              <option value=''>Select Branch</option>
                    {
                        Branches.map((item, i)=>{
                            return (
                                <option key={i} value={item.branch}>{item.branch}</option>
                            )
                        })
                    }
              </select>
            </div>
          </div>
        </div>)                  
        default:
          break;
      } 
    }


    const handleSubmit = async (e) =>{
      e.preventDefault();
      const token = sessionStorage.getItem('token');
      var requestObj = { start_date: selectedStart, end_date: selectedEnd, branch : selectedBranch, location:selectedLocation}
      let res = await request('branchreports/rejects', token).post(requestObj);
      let json = await res.json();
      if(json == ''){alert('No Results are Found');}
      setData(json);
      reset();
   }
    const props = useSpring({
      opacity:1,
      from: {opacity:0}
    })
    return (
      <animated.div style={props}>
      <Fragment>
      <section className="mx-auto col-sm-12 p-3 m-0 rounded">
        <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Branch Reject Reports</h4>
        <div className="headTwo pt-4">
        <form onSubmit={handleSubmit}>
            {
              Locations.length !== 0 ? 
                <div>
                  {
                    branchSelect()
                  }
                  <div className="d-flex row justify-content-between mt-3">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 FieldDiv">
                      <label htmlFor="exampleInputPassword1" className="mb-0" style={{marginRight:'10px'}}>From Date</label>
                      <DatePicker  className="form-control DateField" selected={startDate} onChange={date => startDateChange(date)} required placeholderText="Select Date"  />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 FieldDiv">
                      <label htmlFor="exampleInputPassword1" className="mb-0" style={{marginRight:'10px' }}>To Date</label>
                      <DatePicker className="form-control DateField" minDate={startDate} selected={endDate} onChange={date => endDateChange(date)} required placeholderText="Select Date" />
                    </div>
                  </div>
                </div> : 
                <div style={{padding : '25px'}}>
                  <BounceLoader css={css`display: block; margin: 0 auto; border-color: red;`} size={50} />
                </div>
            }
          
          <div className="d-flex" style={{ width: "100%" }}>
            <Button className="mx-auto" type="submit" variant="contained" color="primary">Submit</Button>
          </div>
          </form>
        </div>
      </section>

      <table className="table table-bordered">
        <thead>
          <tr>
            <th scope="col">S.No</th>
            <th scope="col">Customer Name</th>
            <th scope="col">Product Name</th>
            <th scope="col">Mobile Number</th>
            <th scope="col">Email ID</th>
            <th scope="col">Location</th>
            <th scope="col">TC ID</th>
            <th scope="col">TC Name</th>
            <th scope="col">Branch</th>
          </tr>
        </thead>
        <tbody>
          {
            data.map((item, index) => {
              return (
                <tr key={index}>
                  <th scope="row">{index + 1}</th>
                  <td><small>{item.first_name} {item.last_name}</small></td>
                  <td><small>{item.product}</small></td>
                  <td><small>{item.mobile}</small></td>
                  <td><small>{item.email}</small></td>
                  <td><small>{item.location}</small></td>
                  <td><small>{item.employee_id}</small></td>
                  <td><small>{item.firstname}</small></td>
                  <td><small>{item.branch}</small></td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
      </Fragment>
      </animated.div>
    );
  };


  export default Rejects;