import React, {Fragment, useState, useEffect, useRef} from "react";
import {useSpring, animated} from "react-spring";
import Button from "@material-ui/core/Button";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import "./new_customer.css";
import DatePickerr from 'react-date-picker';
import { request, dateFormat } from '../helper';
import { useAlert } from "react-alert";

const AddNewCustomer = (props) => {
    ////////////////////////////////////////////////// STORING INPUT VALUES USING USESTATE()
    const [dob, onChange] = useState("");
    const [defaultPermanentAddress, setDefaultPermanentAddress] = useState('')
    const [branches, setBranches] = useState([]);
    const [locations, setLocations] = useState([]);
    const [selectedAppointment, onChangeAppointment] = useState();
    const [selectionStatus, setSelectionStatus] = useState('');
    const [EmployeeId, setEmployeeId] = useState('');
    const [callBackDate, setCallBackDate] = useState(null)
    const [remark, setRemark] = useState(null);
    const [reason, setReason] = useState(null);
    const [selectedLocation, setSelectedLocation] = useState('');
    const [permanentAddressMode, setPermanentAddressMode] = useState(false)




    ////////////////////////////////////////////////// STORING INPUT VALUES USING USEREF()
    const title = useRef(null);
    const firstname = useRef(null);
    const lastname = useRef(null);
    const mobile = useRef(null);
    const email = useRef(null);
    const companyname = useRef(null);
    const salaryamount = useRef(null);
    const product = useRef(null);
    const location = useRef(null);
    const branch = useRef(null);
    const selectoflead = useRef(null);
    const status = useRef(null);
    // const line1 = useRef(null);
    // const line2 = useRef(null);
    // const city = useRef(null);
    // const district = useRef(null);
    // const state = useRef(null);
    // const country = useRef(null);
    // const pincode = useRef(null);
    // const permanentAddress = useRef(null);
    // const perLineOne = useRef(null);
    // const perLineTwo = useRef(null);
    // const perCityName = useRef(null);
    // const perDistrict = useRef(null);
    // const perState = useRef(null);
    // const perCountry = useRef(null);
    // const perPincode = useRef(null);
    
    ////////////////////////////////////////////////// USE EFFECT (To load branches in selction)
    useEffect(async () => {
      if(sessionStorage.length === 0){return;}

        (async () => {
          const token = sessionStorage.getItem('token');
          let res = await request("branch/all", token).get();
          let json = await res.json();
          setBranches(json.message);
          const locationsArray = [];
          json.message.map( location => {
              if(!locationsArray.includes(location.location)){
                  locationsArray.push(location.location);
              }
            })
            setLocations(locationsArray);
        })();
        const token = sessionStorage.getItem('token');
        const data = await request(`employee/select/employeeDetails`, token).get();
        const employeeDetails = await data.json();
        setEmployeeId(employeeDetails.message[0].employee_id);
    }, []);


    //////////////////////////////////////////////////INPUT VALIDATION HANDLERS
    const validationHandlers = (e)=>{

    }

                    //     <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    //         <label htmlFor="exampleInputEmail1" className="mb-0">Company Name</label>
                    //         <input type="text" className="form-control inputField" id="exampleInputEmail1" placeholder="Company Name" required aria-describedby="emailHelp" ref={companyname} autoComplete="off" />
                    //     </div>
                    //     <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    //         <label htmlFor="exampleInputPassword1" className="mb-0">Salary Amount</label>
                    //         <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="Salary Amount" required ref={salaryamount} autoComplete="off" />
                    //     </div>
                    //     <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    //         <label htmlFor="exampleInputEmail1" className="mb-0">Product Name</label>
                    //         <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="Product Name" required ref={product} autoComplete="off" />
                    //     </div>
                    // </div>

    ////////////////////////////////////////////////// ON CHANGE HANDLER FOR STATUS
    let selectionStatusHandler = () => {
        switch (selectionStatus) {
          case "Appointment":
            return (
              <Fragment>
                {/* <div className="container"> */}
                <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputPassword1" className="mb-0">Remark</label>
                      <input type="text" className="form-control inputField inputField"  onChange={(e) => setRemark(e.target.value)} name="cheque_number" id="exampleInputPassword1" placeholder="Remark" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      
                    </div>
                  </div>
                 {/* </div> */}
              </Fragment>
            );
          case "Callback":
            return (
              <Fragment>
                {/* <div className="container" > */}
                <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Callback Date <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                      <div className="form-control inputField">
                        <DatePicker className="dateField" controls={['calendar']} selected={callBackDate} onChange={setCallBackDate} required placeholderText="Select Date"  />
                      </div>
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputPassword1" className="mb-0">Remark</label>
                      <input type="text" className="form-control inputField inputField" name="bank_name" onChange={(e) => setRemark(e.target.value)} id="exampleInputPassword1" placeholder="Remark" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  </div>
                {/* </div> */}
              </Fragment>
            );
          case "Reject":
            return (
              <Fragment>
                {/* <div className="container"> */}
                <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Reason</label>
                      <input type="text" className="form-control inputField inputField" name="account_number" onChange={(e) => setReason(e.target.value)} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Reason" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                     
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  </div>
                {/* </div> */}
    
              </Fragment>
            );
            case "Closer":
            return (
              <Fragment>
                {/* <div className="container"> */}
                <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Reason</label>
                      <input type="text" className="form-control inputField inputField" name="account_number" onChange={(e) => setReason(e.target.value)} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Reason" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                     
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  </div>
                {/* </div> */}
    
              </Fragment>
            );
          default:
            return (null);
        }
      }


    ////////////////////////////////////////////////// ON SUBMIT EVENT
    const onSubmitHandler = async(e) => {
        e.preventDefault();
        let date;
        let datofbirth = dob;
        if(datofbirth !== ''){
           date = datofbirth.toLocaleDateString();
        }
        let appointmentdateToLocal = selectedAppointment.toLocaleDateString();
        
        //////////////////////////////////////////// INPUT VALIDATIONS 
        let callBackDateToLocal = null;
        if(callBackDate){
                callBackDateToLocal = callBackDate.toLocaleDateString();
        }

        if(isNaN(mobile.current.value) || mobile.current.value.length !== 10 ){
            // alert.show("Enter a valid mobile number")
            alert("Enter a valid mobile number");
            return ;
        }

        ///////////////////////////////////////// OBJECT TO STORE ALL VALUES
        const formData = {
            employee_id : EmployeeId,
            title: title.current.value,
            first_name: firstname.current.value,
            last_name: lastname.current.value,
            mobile : mobile.current.value,
            email : email.current.value,
            dob: date ? dateFormat(date) : null,
            company_name: companyname.current.value,
            salary_amount: Number(salaryamount.current.value),
            product: product.current.value,
            location: selectedLocation,
            branch: branch.current.value,
            source_lead:selectoflead.current.value,
            appointment_date:dateFormat(appointmentdateToLocal),
            status : status.current.value,
            callback_date : callBackDateToLocal === null ? null : dateFormat(callBackDateToLocal),
            reason : reason,
            remark : remark,
            // present_line1: line1.current.value,
            // present_line2: line2.current.value,
            // present_city: city.current.value,
            // present_district: district.current.value,
            // present_state: state.current.value,
            // present_country: country.current.value,
            // present_pincode: Number(pincode.current.value),
            // present: permanentAddress.current.checked,
            // permanent_line1: perLineOne.current === null ? line1.current.value : perLineOne.current.value,
            // permanent_line2: perLineTwo.current === null ? line2.current.value : perLineTwo.current.value,
            // permanent_city: perCityName.current === null ? city.current.value : perCityName.current.value,
            // permanent_district: perDistrict.current === null ? district.current.value : perDistrict.current.value,
            // permanent_state: perState.current === null ? state.current.value : perState.current.value,
            // permanent_country: perCountry.current === null ? country.current.value : perCountry.current.value,
            // permanent_pincode: perPincode.current === null ? Number(pincode.current.value) : Number(perPincode.current.value),
        }

        setDefaultPermanentAddress(formData);
        const token = sessionStorage.getItem('token');
        /////////////////////////////////////////////////// SENDIND DATA TO DATABASE
        let res = await request("customer/add", token).post(formData);
        let json = await res.json();

        console.log(formData)

        if(json.status === 200) {
          alert("Customer added successfully")
        }
        if(json.status === 302) {
          alert("Customer already exist")
        }

        if(window.location.reload()){
          props.history.push({ pathname: `/Home`})
        }
    }

    ////////////////////////////////////////////////// RETURN CONTENT
    return (
        <Fragment>
            <section className="mx-auto col-sm-12 p-3 m-0 rounded ">
                    <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Customer</h4>
                    <div className="headTwo pt-4">
                    <form onSubmit={(e) => onSubmitHandler(e)}>
                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Title <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <div>
                            <select className="form-control inputField" name="tso_id" required ref={title}>
                                <option value="">Select</option>
                                <option value="Mr">Mr</option>
                                <option value="Mrs">Mrs</option>
                                <option value="Miss">Miss</option>
                            </select>
                            </div>
                        </div>

                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" style={{ display: "block" }}>
                            <label htmlFor="exampleInputPassword1" className="mb-0">First Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <input  type="text"  className="form-control inputField" id="exampleInputPassword1" placeholder="First Name" required ref={firstname} autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Last Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="Last Name" required ref={lastname} autoComplete="off" />
                        </div>
                    </div>


                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 ">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Mobile Number <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" aria-describedby="emailHelp" required placeholder="Mobile Number" ref={mobile} autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
                            <input type="email" className="form-control inputField" id="exampleInputPassword1" placeholder="Email"  ref={email} autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Date of Birth</label>
                            <DatePickerr className="form-control inputField"   maxDate={new Date()}  value={dob}  onChange={onChange} placeholderText="Select Date"  />
                        </div>
                    </div>


                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Company Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" placeholder="Company Name" required aria-describedby="emailHelp" ref={companyname} autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Salary Amount <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="Salary Amount" required ref={salaryamount} autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Product Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="Product Name" required ref={product} autoComplete="off" />
                        </div>
                    </div>



                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Location <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            {/* <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="Location" required ref={location} autoComplete="off" /> */}
                            <select required className="form-control inputField" id="location" required onChange ={(e) => setSelectedLocation(e.target.value)} name="location" >
                                <option value="">Select Location</option>
                                {
                                  locations.map((item, index) => {
                                    return (
                                      <option key={index} value={item}>
                                        {item}
                                      </option>
                                    )
                                  })
                                }
                            </select>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Branch Name <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            {/* <input type="text" className="form-control inputField" id="exampleInputEmail1" aria-describedby="emailHelp" required placeholder="Branch Name" ref={branch} autoComplete="off" /> */}
                            <select required className="form-control inputField" ref={branch} name="title">
                            <option value="">Select Branch</option>
                            {
                              branches.map((item, index) => {
                                if(item.location === selectedLocation){
                                  return (
                                    <option key={item.branch + index} value={item.branch}>
                                      {item.branch}
                                    </option>
                                  )
                                }
                              })
                            }
                            </select>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" >
                            <label htmlFor="exampleInputPassword1" className="mb-0">Select Of Leads <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <select className="form-control inputField" ref={selectoflead}>Select Of Leads
                                <option>Select Source</option>
                                <option>Self</option>
                                <option>Digital</option>
                                <option>TeleCalling</option>
                                <option>Reference</option>
                            </select>
                        </div>
                    </div>

                
                    <div className="d-flex row justify-content-between" >
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Appointment Date <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <div className="form-control inputField">
                              <DatePicker  className="dateField" controls={['calendar']}  selected={selectedAppointment} onChange={onChangeAppointment} required placeholderText="Select Date"  />
                            </div>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                        <label htmlFor="exampleInputPassword1" className="mb-0" >Status <i className="fas fa-star-of-life" style = {{color : 'red', fontSize : '6px'}}></i></label>
                            <select className="form-control inputField" name="payment_mode" id="mop" ref={status} aria-required="true" aria-invalid="false" onChange={(e) => setSelectionStatus(e.target.value)}>
                            <option>Select Source</option>
                            <option >Appointment</option>
                            <option>Callback</option>
                            <option>Reject</option>
                            <option>Closer</option>
                            </select>
                            
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    
                        </div>
                    </div>
                            
                    {selectionStatusHandler()}

                    {/* <div className="d-flex justify-content-center"><h5>Present Address</h5></div>
                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" aria-describedby="emailHelp" required placeholder="Line 1" ref={line1} autoComplete="off"
                            />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="Line 2" required ref={line2} autoComplete="off"
                            />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" placeholder="City Name" required ref={city} autoComplete="off"
                            />
                        </div>
                    </div>

                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" aria-describedby="emailHelp" required placeholder="District" ref={district} autoComplete="off"
                            />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
                            <select className="form-control inputField" required ref={state} >
                            <option value="Andhra Pradesh">Andhra Pradesh</option>
                            <option value="Kerala">Kerala</option>
                            <option value="Karnataka">Karnataka</option>
                            <option value="Telangana">Telangana</option>
                            <option value="Tamilnadu">Tamilnadu</option>
                            <option value="New Delhi">New Delhi</option>
                            <option value="Haryana">Haryana</option>
                            <option value="Jammu and Kashmir">Jammu and Kashmir</option>
                            <option value="Uttar Pradesh">Uttar Pradesh</option>
                            <option value="Punjab">Punjab</option>
                            <option value="Uttarakhand">Uttarakhand</option>
                            <option value="Goa">Goa</option>
                            <option value="Maharashtra">Maharashtra</option>
                            <option value="Rajasthan">Rajasthan</option>
                            <option value="Gujarat">Gujarat</option>
                            <option value="Bihar">Bihar</option>
                            <option value="Orissa">Orissa</option>
                            <option value="Jharkhand">Jharkhand</option>
                            <option value="West Bengal">West Bengal</option>
                            <option value="Chattisgarh">Chattisgarh</option>
                            <option value="Madhya Pradesh">Madhya Pradesh</option>
                            <option value="Himachal Pradesh">Himachal Pradesh</option>
                            <option value="Puducherry">Puducherry</option>
                            <option value="Assam">Assam</option>
                            <option value="Arunachal Pradesh">Arunachal Pradesh</option>
                            <option value="Mizoram">Mizoram</option>
                            <option value="Meghalaya">Meghalaya</option>
                            <option value="Manipur">Manipur</option>
                            <option value="Nagaland">Nagaland</option>
                            <option value="Sikkim">Sikkim</option>
                            <option value="Tripura">Tripura</option>
                            </select>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
                            <select className="form-control inputField" ref={country} required >
                            <option value=" ">Select Country</option>
                            <option value="India">India</option>
                            </select>
                        </div>
                    </div>

                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" aria-describedby="emailHelp" required placeholder="Pin Code" ref={pincode} autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                    </div>

                    <div>
                        <input type="checkbox" id="check" name="check" ref={permanentAddress} onChange={(e) => setPermanentAddressMode(e.target.checked)} />
                        <label htmlFor="check" style={{ color: "#aeaeae" }}><b>&nbsp;&nbsp;Same as present address please click it</b></label>
                    </div> */}

                    {/* {permanentAddressHandler()} */}

                    <div className="d-flex" style={{ width: "100%" }}>
                    <Button className="mx-auto" type="submit" variant="contained" color="primary">Submit</Button>
                    </div> 
                    </form>
                    </div>
            </section>
        </Fragment>
    )
}

export default AddNewCustomer;