import { render } from "@testing-library/react";
import React, { Fragment, useState, useRef } from "react";
import {request, dateFormat} from "../helper";
import { useEffect } from "react";
import '../DARIS.css';
import Button from "@material-ui/core/Button";
import { withRouter } from "react-router-dom"; 
import DatePicker from "react-datepicker";
import DatePickerr from 'react-date-picker';


const ShowCustomers = (props)=>{
    const [changedDOB, setChangedDOB] = useState(new Date());
    const [changedAppointmentDate, setChangedAppointmentDate] = useState(new Date());
    const [changedCallbackDate, setChangedCallbackDate] = useState(new Date());
    const [customerDetails, setCustomerDetails] = useState('');
    const [statusSelection, setStatusSelection] = useState('')
    const [branches, setBranches] = useState([]);
    const [remark, setRemark] = useState('');
    const [reason, setReason] = useState('');
    const enteredMobileNumber = useRef('');




    const states = ['Andhra Pradesh', 'Kerala', 'Karnataka', 'Telangana', 'Tamilnadu', 'New Delhi', 'Haryana', 'Jammu and Kashmir', 'Uttar Pradesh', 'Punjab', 'Uttarakhand', 'Punjab', 'Uttarakhand', 'Goa', 'Maharashtra', 'Rajasthan'
    ,'Gujarat', 'Bihar', 'Orissa', 'Jharkhand', 'West Bengal', 'Chattisgarh', 'Madhya Pradesh', 'Sikkim', 'Tripura', 'Himachal Pradesh', 'Puducherry', 'Assam', 'Arunachal Pradesh', 'Mizoram', 'Meghalaya', 'Manipur', 'Nagaland']
    const statusArray = ["Appointment", "Callback", "Closer", "Reject"];
    const leads = ['Self', 'Digital', 'TeleCalling', 'Reference'];
    const titles = ["Mr", 'Mrs', 'Miss'];

    ////////////////////////////////////////////////// USE EFFECT (To load branches in selction)
    useEffect(() => {
        if(sessionStorage.length === 0){return;}
        (async () => {
            try {
                const token = sessionStorage.getItem('token');
                let res = await request("branch/all", token).get();
                let json = await res.json();
                setBranches(json.message);     
            } catch (error) {
                alert(error)   
            }
        })();

        setCustomerDetails(props.location.state);
        setChangedDOB(new Date(props.location.state.dob));
        setChangedAppointmentDate(new Date(props.location.state.appointment_date));
        if(props.location.state.callback_date !== null){
            setChangedCallbackDate(new Date(props.location.state.callback_date));
        }
        setReason(props.location.state.reason);
        setRemark(props.location.state.remark);
        setStatusSelection(props.location.state.status)
    }, []);
      
    ///////////////////////////////////////////////////////// USEREF TO STORE DATA
    const title = useRef(null);
    const firstname = useRef(null);
    const lastname = useRef(null);
    const mobile = useRef(null);
    const email = useRef(null);
    const companyname = useRef(null);
    const salaryamount = useRef(null);
    const product = useRef(null);
    const location = useRef(null);
    const branch = useRef(null);
    const selectoflead = useRef(null);
    const status = useRef(null);
    const line1 = useRef(null);
    const line2 = useRef(null);
    const city = useRef(null);
    const district = useRef(null);
    const state = useRef(null);
    const country = useRef(null);
    const pincode = useRef(null);
    const permanentAddress = useRef(null);
    const perLineOne = useRef(null);
    const perLineTwo = useRef(null);
    const perCityName = useRef(null);
    const perDistrict = useRef(null);
    const perState = useRef(null);
    const perCountry = useRef(null);
    const perPincode = useRef(null);
  
    
    // const onSubmitHandler = async (e)=>{
    //     e.preventDefault();

    //     if(enteredMobileNumber.current.value.length !== 10){
    //         alert("Please Enter 10 Digits Valid Number");
    //         return;
    //     }

    //     const mobileNumber = {
    //             mobile : enteredMobileNumber.current.value,
    //     }

    //     const res = await request('existingcustomer/customerdetails').post(mobileNumber);
    //     const json = await res.json();
    //     let responseData = null;
    //     if(res.status === 200){
    //         responseData = json;
    //     }

    //     if(res.status !== 200 || json.length === 0 || !json){
    //         alert("No Customer with this mobile number");
    //         return;
    //     }

    //     setCustomerDetails(responseData[0]);
    //     setChangedDOB(new Date(responseData[0].dob));
    //     setChangedAppointmentDate(new Date(responseData[0].appointment_date));
    //     if(responseData[0].callback_date !== null){
    //         setChangedCallbackDate(new Date(responseData[0].callback_date));
    //     }
    //     setReason(responseData[0].reason);
    //     setRemark(responseData[0].remark);
    //     setStatusSelection(responseData[0].status)
    // }

    const updatedSubmitHandler = async (e)=> {
        e.preventDefault();
        const token = sessionStorage.getItem('token');

        let datofbirth = changedDOB;
        let date = datofbirth.toLocaleDateString();
        let appointmentdateToLocal = changedAppointmentDate.toLocaleDateString();
        
        //////////////////////////////////////////// INPUT VALIDATIONS 
        let callBackDateToLocal = null;
        if(changedCallbackDate){
                callBackDateToLocal = changedCallbackDate.toLocaleDateString();
        }


        const formData = {
            title: title.current.value,
            first_name: firstname.current.value,
            last_name: lastname.current.value,
            mobile : mobile.current.value,
            email : email.current.value,
            dob: date ? dateFormat(date) : null,
            company_name: companyname.current.value,
            salary_amount: Number(salaryamount.current.value),
            product: product.current.value,
            location: location.current.value,
            branch: branch.current.value,
            source_lead:selectoflead.current.value,
            appointment_date:dateFormat(appointmentdateToLocal),
            status : status.current.value,
            callback_date : callBackDateToLocal === null ? '' : dateFormat(callBackDateToLocal),
            reason : reason,
            remark : remark,
        }

        let res = await request("customer/update", token).post(formData);
        let json = await res.text();
        if(res.status === 200){
            alert("Employee details updated successfully")
        }
    }


    let selectionStatusFunc = () => {
        switch (statusSelection) {
          case "Appointment":
            return (
              <Fragment>
                {/* <div className="container"> */}
                <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputPassword1" className="mb-0">Remark</label>
                      <input type="text" className="form-control inputField" name="cheque_number" defaultValue={remark} onChange={(e) => setRemark(e.target.value)} id="exampleInputPassword1" placeholder="Remark" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      
                    </div>
                  </div>
                 {/* </div> */}
              </Fragment>
            );
          case "Callback":
            return (
              <Fragment>
                {/* <div className="container" > */}
                <div className="d-flex row  justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Callback Date</label>
                      <div className="form-control inputField">
                        <DatePicker controls={['calendar']} className="dateField" selected={new Date(changedCallbackDate)} onChange={setChangedCallbackDate} required placeholderText="Select Date"  />
                      </div>
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputPassword1" className="mb-0">Remark</label>
                      <input type="text" className="form-control inputField" name="bank_name" defaultValue={remark}  onChange={(e) => setRemark(e.target.value)} id="exampleInputPassword1" placeholder="Remark" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  </div>
                {/* </div> */}
              </Fragment>
            );
          case "Reject":
            return (
              <Fragment>
                {/* <div className="container"> */}
                <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Reason</label>
                      <input type="text" className="form-control inputField" name="account_number" defaultValue={reason} onChange={(e) => setReason(e.target.value)} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Reason" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                     
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  </div>
                {/* </div> */}  
              </Fragment>
            );
            case "Closer":
            return (
              <Fragment>
                {/* <div className="container"> */}
                <div className="d-flex row justify-content-between">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputEmail1" className="mb-0">Reason</label>
                      <input type="text" className="form-control inputField" name="account_number" defaultValue={reason} onChange={(e) => setReason(e.target.value)} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Reason" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                     
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                  </div>
                {/* </div> */}
              </Fragment>
            );
          default:
            return (null);
        }
      }


    return(
        <Fragment>

        <section className="mx-auto col-sm-12 p-3 m-0 rounded ">
                <form onSubmit={ (e) => updatedSubmitHandler(e)}>
                    <div className="d-flex row justify-content-between">
                            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                                <label htmlFor="exampleInputEmail1" className="mb-0">Title</label>
                                <div>
                                <select className="form-control inputField" ref={title} name="tso_id"  required >
                                    <option value="">Select</option>
                                    {
                                      titles.map( title => {
                                          if(title === customerDetails?.title){
                                              return <option selected value={title}>{title}</option>
                                          }
                                          return <option value={title}>{title}</option>
                                      })  
                                    }
                                </select>
                                </div>
                            </div>
                            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3" style={{ display: "block" }}>
                                <label htmlFor="exampleInputPassword1" className="mb-0">First Name</label>
                                <input  type="text"  className="form-control inputField" id="exampleInputPassword1" ref={firstname} defaultValue={customerDetails?.first_name  } placeholder="First Name" required autoComplete="off" />
                            </div>
                            <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                                <label htmlFor="exampleInputPassword1" className="mb-0">Last Name</label>
                                <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={lastname} defaultValue={customerDetails?.last_name} placeholder="Last Name" required autoComplete="off" />
                            </div>
                    </div>

                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 ">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Mobile Number</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={mobile} defaultValue={customerDetails?.mobile} aria-describedby="emailHelp" required placeholder="Mobile Number"  autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Email</label>
                            <input type="email" className="form-control inputField" id="exampleInputPassword1" ref={email} defaultValue={customerDetails?.email} placeholder="Email" autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Date of Birth</label>
                            <div className="form-control inputField">
                            <DatePickerr className="form-control inputField"  value={new Date(changedDOB)} onChange={setChangedDOB} placeholderText="Select Date"   />
                            </div>
                        </div>
                    </div>



                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Company Name</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={companyname}  defaultValue={customerDetails?.company_name} placeholder="Company Name" required aria-describedby="emailHelp"  autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Salary Amount</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={salaryamount} defaultValue={customerDetails?.salary_amount} placeholder="Salary Amount" required  autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Product Name</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={product} defaultValue={customerDetails?.product} placeholder="Product Name" required autoComplete="off" />
                        </div>
                    </div>



                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Location</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={location} defaultValue={customerDetails?.location} placeholder="Location" required autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Branch Name</label>
                            <select required className="form-control inputField" ref={branch} name="title" >
                            <option value="">Select Branch</option>
                            {
                                branches.map((item, index) => {
                                    if(item.branch === customerDetails?.branch){
                                        return <option key={index} selected value={item.branch}>{item.branch}</option>
                                    }
                                return (
                                    <option key={index} value={item.branch}>{item.branch}</option>
                                )
                                })
                            }
                            </select>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Select Of Leads</label>
                            <select required className="form-control inputField" ref={selectoflead}>
                            <option> Select Lead</option>
                                    {
                                        leads.map(lead => {
                                            if(lead === customerDetails?.source_lead){
                                                return <option selected value={lead}>{lead}</option>
                                            }
                                         return <option value={lead}>{lead}</option>
                                        })
                                    }
                            </select>
                        </div>
                    </div>

                    <div className="d-flex row justify-content-between" >
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Appointment Date</label>
                            <div className="form-control inputField">
                                <DatePicker  controls={['calendar']}  className="dateField" selected={new Date(changedAppointmentDate)} onChange={setChangedAppointmentDate} required placeholderText="Select Date"  />
                            </div>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                        <label htmlFor="exampleInputPassword1" className="mb-0" >status</label>
                            <select className="form-control inputField" ref={status} onChange={(e) => setStatusSelection(e.target.value)} name="payment_mode" id="mop" aria-required="true" aria-invalid="false">
                            <option>Select Source</option>
                            {
                                        statusArray.map(s => {
                                            if(s === customerDetails.status){
                                                return <option selected value={s}>{s}</option>
                                            }
                                         return <option value={s}>{s}</option>
                                        })
                                    }
                            </select>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                    </div>

                    {selectionStatusFunc()}               

                    {/* <div className="d-flex justify-content-center"><h5>Present Address</h5></div>
                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={line1} defaultValue={customerDetails?.present_line1} aria-describedby="emailHelp" required placeholder="Line 1"  autoComplete="off"
                            />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={line2} defaultValue={customerDetails?.present_line2} placeholder="Line 2" required  autoComplete="off"
                            />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={city} defaultValue={customerDetails?.present_city} placeholder="City Name" required  autoComplete="off"
                            />
                        </div>
                    </div>  


                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={district} defaultValue={customerDetails?.present_district} aria-describedby="emailHelp" required placeholder="District" autoComplete="off"
                            />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">State</label>
                            <select className="form-control inputField" required ref={state}>
                            <option>Select State</option>
                                    {
                                        states.map(state => {
                                            if(state === customerDetails?.present_state){
                                                return <option selected value={state}>{state}</option>
                                            }
                                         return <option value={state}>{state}</option>
                                        })
                                    }
    
                            </select>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
                            <select className="form-control inputField" required ref={country}>
                            <option value=" ">Select Country</option>
                            <option selected value="India">India</option>
                            </select>
                        </div>
                    </div>  

                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={pincode} defaultValue={customerDetails?.present_pincode}  aria-describedby="emailHelp" required placeholder="Pin Code"  autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                    </div>

                    <div className="d-flex justify-content-center"><h5>Permanent Address</h5></div>
                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Line 1</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={perLineOne} defaultValue={customerDetails.permanent_line1 === null ? customerDetails.present_line1 : customerDetails.permanent_line1 } aria-describedby="emailHelp" placeholder="Line 1"  required  autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Line 2</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={perLineTwo} defaultValue={customerDetails.permanent_line2 === null ? customerDetails.present_line2 : customerDetails.permanent_line2 } placeholder="Line 2"  required  autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">City Name</label>
                            <input type="text" className="form-control inputField" id="exampleInputPassword1" ref={perCityName} defaultValue={customerDetails.permanent_city === null ? customerDetails.present_city : customerDetails.permanent_city } placeholder="City Name" required autoComplete="off" />
                        </div>
                    </div>           
                    

                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">District</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={perDistrict} defaultValue={customerDetails.permanent_district === null ? customerDetails.present_district : customerDetails.permanent_district } aria-describedby="emailHelp"  required placeholder="District" autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0" >State</label>
                            <select className="form-control inputField" required ref={perState}>
                                <option value="">Select State</option>
                                {
                                        states.map(state => {
                                            if(customerDetails.permanent_state === null){
                                                if(state === customerDetails.present_state){
                                                    return <option selected value={state}>{state}</option>
                                                }  
                                            }else{
                                                if(state === customerDetails.permanent_state){
                                                    return <option selected value={state}>{state}</option>
                                                }
                                            }
                                            
                                         return <option value={state}>{state}</option>
                                        })
                                    }
                            </select>
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputPassword1" className="mb-0">Country</label>
                            <select className="form-control inputField" required ref={country}>
                            <option value=" ">Select Country</option>
                            <option selected value="India">India</option>
                            </select>
                        </div>
                    </div>
                    <div className="d-flex row justify-content-between">
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                            <label htmlFor="exampleInputEmail1" className="mb-0">Pin Code</label>
                            <input type="text" className="form-control inputField" id="exampleInputEmail1" ref={perPincode} aria-describedby="emailHelp"  defaultValue={customerDetails.permanent_pincode === null ? customerDetails.present_pincode : customerDetails.permanent_pincode } required placeholder="Pin Code" autoComplete="off" />
                        </div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                        <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
                    </div> */}
                    <div className="d-flex" style={{ width: "100%" }}>
                    <Button className="mx-auto" type="submit" variant="contained" color="primary">Update</Button>
                    </div>
                </form> 
        </section>
        </Fragment>
    );
}


export default withRouter(ShowCustomers)