import React, { Component, Fragment, createRef } from "react";
import Button from "@material-ui/core/Button";
import "../Home.css";
import DatePicker from "react-datepicker";
import {request, dateFormat} from "../helper";
import { withRouter } from "react-router-dom"; 
import { Search } from "@material-ui/icons";




class SearchCustomer extends Component{

  constructor(props){
    super(props);
    this.enteredMobileNumber = createRef(null);
    this.state = {
      startDate : '',
      endDate : '',
      selectedStartDate : '',
      selectedEndDate : '',
      employeeId : '',
    }
  }  

  startDateChange = (e) => {
    this.setState({
      startDate : e,
      selectedStartDate : dateFormat(new Date(e).toLocaleDateString())
    })
  }

  endDateChange = (e) => {
    this.setState({
      endDate : e,
      selectedEndDate : dateFormat(new Date(e).toLocaleDateString())
    })
  }
  async componentDidMount(){
      // const res = await request('existingcustomer/allcustomers');
      // const json = await res.json();
      if(sessionStorage.length === 0) return;
      const token = sessionStorage.getItem('token');
      const data = await request(`employee/select/employeeDetails`, token).get();
      const employeeDetails = await data.json();
      this.setState({employeeId : employeeDetails.message[0].employee_id});
  }

  onSubmitHandlerDates = async (e) => {
    e.preventDefault();
    const token = sessionStorage.getItem('token');
    const requestObject = {employee_id :this.state.employeeId, start_date : this.state.selectedStartDate, end_date : this.state.selectedEndDate};
    const res = await request('existingcustomer/allcustomers', token).post(requestObject);
    const json = await res.json();

    let responseData = null;
    if(res.status === 200){
        responseData = json;
    }

    if(res.status !== 200 || json.length === 0 || !json){
        alert("No Customer between selected dates");
        return;
    }
    
    this.props.history.push({
        pathname:"/Home/CustomersTable",
        state: responseData
    });

}


onSubmitHandler = async (e) => {
    e.preventDefault();

    if(this.enteredMobileNumber.current.value.length !== 10){
        alert("Please Enter 10 Digits Valid Number");
        return;
    }


    const mobileNumber = {
            mobile : this.enteredMobileNumber.current.value,
    }
    const token = sessionStorage.getItem('token');
    const res = await request('existingcustomer/customerdetails', token).post(mobileNumber);
    const json = await res.json();
    let responseData = null;
    if(res.status === 200){
        responseData = json;
    }


    if(res.status !== 200 || json.length === 0 || !json){
        alert("No Customer with this mobile number");
        return;
    }
    
    this.props.history.push({
        pathname:"/Home/Existingcustomer",
        state: responseData[0]
       });

}

render(){
    return(
        <Fragment>
            <section className="mx-auto col-sm-7 p-3 m-0 rounded">
              <h5 className="text-center mx-auto col-sm-11 mb-3 heading p-2" style={{borderRadius:"5px", border:"1px solid #fff", padding: "10px 30px", boxShadow:" 0 4px 8px 0 rgba(0,0,0,.2), 0 6px 20px 0 transparent"}}>Customer Details</h5>
              <div className="headTwo pt-4" style={{borderRadius:"10px", border:"1px solid #fff", padding: "5px 30px", boxShadow:" 0 4px 8px 5px rgba(0,0,0,.2), 0 6px 20px 5px transparent"}}>
                <form onSubmit={(e) => this.onSubmitHandler(e)}>
                  <div className="form-group pt-0 pb-0 pr-3 pl-3" >
                    <label htmlFor="exampleInputEmail1" className="mb-0"> Enter Customer Mobile Number </label>
                    <input type="text"  name="location" required className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Mobile Number" ref={this.enteredMobileNumber} autoComplete="off" style={{display:"block", backgroundColor: "White", color: "#495057", lineHeight: "1.5", fontSize: "1rem", borderRadius:"5px", border:"1px solid #fff", padding: "5px 30px", boxShadow:" 0 4px 8px 5px rgba(0,0,0,.2), 0 6px 20px 5px transparent"}}/>
                  </div>
                  <div className="d-flex" style={{ width: "100%" }}>
                    {/* <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} /> */}
                    <Button className="mx-auto" type="submit" variant="contained" color="primary">Get Details</Button>
                  </div>
                </form>
              </div>
              <div className="headTwo pt-4" >
                <div className="headTwo pt-4" style={{borderRadius:"10px", border:"1px solid #fff", padding: "5px 30px", boxShadow:" 0 4px 8px 5px rgba(0,0,0,.2), 0 6px 20px 5px transparent"}}>
                  <form onSubmit={(e) => this.onSubmitHandlerDates(e)}>
                  <div className="d-flex row justify-content-between mt-3">
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputPassword1" className="mb-0"  style={{marginRight: "22px"}}>From Date</label>
                      <DatePicker className="form-control" style={{ zIndex : '1500'}} selected={this.state.startDate} onChange={date => this.startDateChange(date)} required placeholderText="Select Date" />
                    </div>
                    <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                      <label htmlFor="exampleInputPassword1" className="mb-0" style={{marginRight: "40px"}}>To Date</label>
                      <DatePicker className="form-control" style={{zIndex : '1500'}} minDate={this.state.startDate}  selected = {this.state.endDate} onChange={date => this.endDateChange(date)} required placeholderText="Select Date" />
                    </div>
                  </div>
                  <div className="d-flex" style={{ width: "100%" }}>
                      <Button className="mx-auto" type="submit" variant="contained" color="primary">Submit</Button>
                  </div>
                  </form>
                </div>
              </div>
            </section>
        </Fragment>
    );
}
}

export default withRouter(SearchCustomer);