import React, { useState, useEffect, Fragment, Component } from 'react';
import { useHistory } from "react-router";
import {withRouter, useRouteMatch, Switch, Route, Link, useParams, matchPath  } from 'react-router-dom';
import { request } from '../helper';
import { useSpring, animated } from "react-spring";
import Popup from '../popUp/popUp';
// import PopUpAlert from '../popUp/PopUpAlert';
import { css } from "@emotion/core";
import BounceLoader from "react-spinners/BounceLoader";


class Graphs extends Component {
  constructor(props){
    super(props)
    this.state = {
      role : '',
      graphs : {},
      displayTable : [],
      sideBarToogle : false,
      graphTable : false,
      tableName : '',
    }
  }

  reRenderFunction = async ()=>{
    const token = sessionStorage.getItem('token');
    const graphs = await request(`employee/graphs`, token).get();
    const json = await graphs.json();
    this.setState({graphs : json});
  }

 displayTableHandler = (text) => {
  switch(text){
    case "Appointments" : 
        this.setState({displayTable : this.state.graphs.appointments})
    break;
    case "Callbacks" : 
        this.setState({displayTable : this.state.graphs.callbacks})
    break;
    case "Closers" : 
        this.setState({displayTable : this.state.graphs.closers})
    break;

    default:
      break;
  }
  this.setState({graphTable : true});
}

async componentDidMount() {
  if(sessionStorage.length === 0) {
      this.props.history.push({pathname: `/`,state : 'relogin'});
      return;
  }
    const token = sessionStorage.getItem('token');
    const data = await request(`employee/select/employeeDetails`, token).get();
    const response = await data.json();
    this.setState({role : response.message[0].role})
    this.reRenderFunction();
}

popUpHandler = (text) =>{
  this.reRenderFunction();
  this.setState({graphTable : false});
}


render() {
  return(
    <Fragment>      
      {/* <PopUpAlert /> */}
              <div>
                <div className="Home_main_container"> 
                    <div className="Home_sub_container" onClick={(e) => this.displayTableHandler("Appointments")}>
                      {
                        this.state.graphs.appointments ? 
                        <div>
                          <h4 className="Home_sub_text">Today's Appointment Reports</h4>
                          <h3 className="icon"><i className="far fa-calendar-check "></i></h3>
                          <h1 className="Home_sub_text">{this.state.graphs.appointments?.length}</h1>
                        </div>  : 
                        <div className="Home_BounceLoader"> 
                          <BounceLoader css={css`display: block; margin: 0 auto; border-color: red;`} size={50} />
                        </div> 
                      }
                    </div> 
                    <div className="Home_sub_container" onClick={(e) => this.displayTableHandler("Callbacks")}>
                      {
                        this.state.graphs.callbacks ? 
                        <div>
                          <h4 className="Home_sub_text">Today's Callback Reports</h4>
                          <h3 className="icon"><i className="fas fa-headset"></i></h3>
                          <h1 className="Home_sub_text">{this.state.graphs.callbacks?.length}</h1>
                        </div>  : 
                        <div className="Home_BounceLoader"> 
                          <BounceLoader css={css`display: block; margin: 0 auto; border-color: red;`} size={50} />
                        </div> 
                      } 
                    </div> 
                    <div className="Home_sub_container" onClick={(e) => this.displayTableHandler("Closers")}>
                    {
                        this.state.graphs.closers ? 
                        <div>
                          <h4 className="Home_sub_text">Today's Closer Reports</h4>
                          <h3 className="icon"><i className="far fa-times-circle"></i></h3>
                          <h1 className="Home_sub_text">{this.state.graphs.closers?.length}</h1>
                        </div> : 
                        <div className="Home_BounceLoader"> 
                          <BounceLoader css={css`display: block; margin: 0 auto; border-color: red;`} size={50} />
                        </div> 
                      } 
                    </div> 
                </div>

                <div style={{display : this.state.graphTable ? "initial" : "none"}}>
                  <div className="Home_table" style={{overflowX : "scroll"}}>
                      <table className="table table-bordered">
                        <thead>
                          <tr>
                            <th scope="col">S.No</th>
                            <th scope="col">Customer Name</th>
                            <th scope="col">Product Name</th>
                            <th scope="col">Mobile Number</th>
                            <th scope="col">Location</th>
                            <th scope="col">TC ID</th>
                            <th scope="col">TC Name</th>
                            <th scope="col">Branch</th>
                            <th scope="col">Status</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            this.state.displayTable?.map((item, index) => {
                              return (
                                <tr  key={index}>
                                  <th scope="row">{index + 1}</th>
                                  <td><small>{item.first_name} {item.last_name}</small></td>
                                  <td><small>{item.product}</small></td>
                                  <td><small>{item.mobile}</small></td>
                                  <td><small>{item.location}</small></td>
                                  <td><small>{item.employee_id}</small></td>
                                  <td><small>{item.firstname}</small></td>
                                  <td><small>{item.branch}</small></td>
                                  <td><Popup customerData= {item} handler={(text) => this.popUpHandler(text)}/></td>
                                </tr>
                              )
                            })
                          }
                        </tbody>
                      </table>
                  </div>
                </div>                 
              </div> 
    </Fragment>

  )
}
}

export default withRouter(Graphs);