import React, { Fragment, useState, useEffect } from 'react';
import { withRouter } from "react-router-dom"; 
import {request, dateFormat} from "../helper";


const CustomersTable = (props) => {
    const [customerTable, setCustomerTable] = useState([]);

    useEffect(() => {
      if(sessionStorage.length === 0){return;}

      const array1 = props.location.state.sort(function(a,b){
        return b.id - a.id;
        });
        setCustomerTable(array1);
  }, [])

    
   const onSubmitHandler = async (e, mobileNumber) => {
      e.preventDefault();
  
      const mobileNumberObj = {
              mobile : mobileNumber,
      }
      const token = sessionStorage.getItem('token');
      const res = await request('existingcustomer/customerdetails', token).post(mobileNumberObj);
      const json = await res.json();
      let responseData = null;
      if(res.status === 200){
          responseData = json;
      }
  
  
      if(res.status !== 200 || json.length === 0 || !json){
          alert("No Customer with this mobile number");
          return;
      }
      
      props.history.push({
          pathname:"/Home/Existingcustomer",
          state: responseData[0]
         });
  
  }

    return (
        <Fragment>
              {/* <div className="headTwo pt-4" style={{padding: "5px 30px"}}>
                  <div className="form-group pt-0 pb-0 pr-3 pl-3" >
                    <input type="text"  name="location" required className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Search Customer..."  autoComplete="off" style={{textAlign:'center', display:"block", backgroundColor: "White", color: "#495057", lineHeight: "1.5", fontSize: "1rem", borderRadius:"5px", border:"1px solid #fff", padding: "5px 30px", boxShadow:" 0 4px 8px 5px rgba(0,0,0,.2), 0 6px 20px 5px transparent"}}/>
                  </div> 
              </div>  */}



               <div className="Home_table" style={{overflowX : "scroll"}}>
                  <table className="table table-bordered">
                    <thead>
                      <tr>
                        <th scope="col">S.No</th>
                        <th scope="col">Customer Name</th>
                        <th scope="col">Product Name</th>
                        <th scope="col">Mobile Number</th>
                        <th scope="col">Location</th>
                        <th scope="col">Branch</th>
                        <th scope="col">Status</th>
                        <th scope="col">Appointment Date</th>
                        <th scope="col"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        customerTable?.map((item, index) => {
                          return (
                            <tr  key={index}>
                              <th scope="row">{index + 1}</th>
                              <td><small>{item.first_name} {item.last_name}</small></td>
                              <td><small>{item.product}</small></td>
                              <td><small>{item.mobile}</small></td>
                              <td><small>{item.location}</small></td>
                              <td><small>{item.branch}</small></td>
                              <td><small>{item.status}</small></td>
                              <td><small>{item.appointment_date}</small></td>
                              <td><small><i onClick={(e) => onSubmitHandler(e, item.mobile)} className="far fa-edit"></i></small></td>
                            </tr>
                          )
                        })
                      }
                    </tbody>
                  </table>
              </div>         
        </Fragment>
        // <Fragment>
        //     <DatatablePage customersArray = {customerTable}  />
        // </Fragment>
    )
}
export default withRouter(CustomersTable)