function renderSideBar(path, role) {
  switch (role) {
    case 'Zonal Manager':
    case 'CEO':
      return renderAll(path); 
    case 'Sr.Relationship Manager':
    case 'Regional Manager':
      return renderRelationalManager(path);
    case 'Branch Manager':
      return renderBranchManager(path);
    case "Telecaller":
    case "Team Lead":
    case "Senior Team Lead":
    case 'Relationship Manager':
      return renderTeleCaller(path);
    default:
      return renderAll(path);
  }
}


function returnId(data) {
  if (data === undefined || data.status === 404 || data === "" || ('employee_id' in data) === false) {
    return sessionStorage.getItem('_id');
  }
  return data.employee_id;
}

/////////////////////////////////////////////////////////////// MAIN COMPONENT

const MyComponent = (props) => {
  const [role, setRole] = useState('');
  const [employeeData, setEmployeeData] = useState({});
  const [sideBarToogle, setSideBarToogle] = useState(false);
  const history = useHistory();


  useEffect(async () => {
    if(sessionStorage.length === 0) {
      history.push({pathname: `/`,state : 'relogin'});
      return;
    }
    let response;
    try{
      const token = sessionStorage.getItem('token');
      const data = await request(`employee/select/employeeDetails`, token).get();
      response = await data.json();
      console.log(response);
      setRole(response.message[0].role);
      setEmployeeData(response.message[0]);
    }catch(e){
      alert("Something went wrong, please login again");
    }
}, []);


  
  function logout() {
    if (window.confirm("Are you sure you want to Logout ?")) {
      sessionStorage.clear();
      props.history.push({
        pathname: `/`
      });
    }
  };

  let { path, url } = useRouteMatch();


  const styleProps = useSpring({
      opacity : 1,
      marginLeft : 0,
      from : {opacity : 0, marginLeft:-500}
  });

  var toggleSideBar = () => {
    setSideBarToogle(!sideBarToogle)
  }



  let closeBar = (e) => {
    if(e.target.tagName === 'A'){
      toggleSideBar();
      active(e.target.textContent)
    }
  }

  const active = async (e)=>{
    const allLis = document.querySelectorAll(".pro-menu-item");

  
    for (const li of allLis){
       if(!li.classList.contains("pro-sub-menu")){
        if(li.classList.contains("activeClass")){
          li.classList.remove("activeClass");
          li.querySelector('a').style.color = "inherit";
        }
       }
    }
  
    for(const li of allLis){
      if(!li.classList.contains("pro-sub-menu")){
          if(li.textContent === e){
            li.classList.add('activeClass');
            li.querySelector('a').style.color = "black";
        }
      }
    }
  }
  

  return (
    <Fragment>
    <animated.div style={styleProps}>
    <div className="Home_main_div">
      
      <div className="Home_header_div">
        <div className="Home_sidebar_button" onClick={toggleSideBar}><i className="fas fa-bars"></i></div>
        <h4 className="p-2 align-self-center" style={{ color: "white", marginTop:"9px" }}>DAR for Investment Services</h4>
        <h6 className="m-0 p-0 align-self-center text-white"><strong>Name:</strong> {employeeData.firstname}</h6>
        <h6 className="m-0 p-0 align-self-center text-white"><strong>Location:</strong> {employeeData.location}</h6>
        <h6 className="m-0 p-0 align-self-center text-white"><strong>Role:</strong> {role}</h6>
        {/* <div className="profileDiv"><Link to={`${path}/Profile`}><i className="fas fa-user profile"></i></Link></div> */}
        <button className="btn p-1 align-self-center Home_logout" onClick={() => logout()}>Logout</button>
      </div>
        

      <div className="Home_content">
        
        <div onClick={e => closeBar(e)} className={["Home_sidebar", sideBarToogle ? "pro-sidebar-open" : "pro-sidebar-close" ].join(" ")}>
          {renderSideBar(url, role)}
        </div>
        
        <section className="Home_content_section">
          <Switch>
            <Route exact path={url}>
              <Graphs />
            </Route>
            <Route path={`${url}/AddEmployee`}>
              <AddEmployee />
            </Route>
            <Route path={`${url}/EmployeeDetails`}>
              <EmployeeDetails />
            </Route>
            <Route path={`${url}/BranchDetails`}>
              <BranchDetails />
            </Route>
            <Route path={`${url}/AddDesignation`}>
              <AddDesignation />
            </Route>
            <Route path={`${url}/AddNewCustomer`}>
              <AddNewCustomer />
            </Route>
            <Route path={`${url}/Customers`}>
              <ShowCustomers />
            </Route>
            <Route path={`${url}/Appointments`}>
              <Appointments />
            </Route>
            <Route path={`${url}/Callbacks`}>
              <CallBacks />
            </Route>
            <Route path={`${url}/Closers`}>
              <Closers />
            </Route>
            <Route path={`${url}/Rejects`}>
              <Rejects />
            </Route>
            <Route path={`${url}/Existingcustomer`}>
              <ExistingCustomer  />
            </Route>
            <Route path={`${url}/Branch/Appointments`}>
              < BranchAppointments/>
            </Route>
            <Route path={`${url}/Branch/Callbacks`}>
              <BranchCallBacks />
            </Route>
            <Route path={`${url}/Branch/Closers`}>
              <BranchClosers  />
            </Route>
            <Route path={`${url}/Branch/Rejects`}>
              <BranchRejects  />
            </Route>
            <Route path={`${url}/EmployeeUpdation`}>
              <EmployeeUpdation />
            </Route>
            <Route path={`${url}/CustomersTable`}>
              <CustomersTable />
            </Route>
            
          </Switch>
        </section>
      </div>
    </div>
          
    </animated.div>
    </Fragment>
  );
};

export default MyComponent;