const request = function (url, token) {
  /////////////////////////////////// FOR DEVELOPMENT
  // const URL = `http://localhost:1997/api/${url}`;

  ///////////////////////////////////// FOR DEPLOYMENT
  const URL = `/api/${url}`;

  let postMethod = async (body) => {
    try {
      var response = await fetch(URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json;charset=utf-8', Authorization: `${token}` },
        body: JSON.stringify(body),
      });
    } catch (e) {
      alert(e);
    }

    return await response;
  };

  let getMethod = async () => {
    return await fetch(URL, {
      method: 'GET',
      headers: { Authorization: `${token}` },
    });
  };

  return {
    post: postMethod,
    get: getMethod,
  };
  // net pre = gross - gross * (18 / 100)
  // rev = net - net * (rev / 100)
};

let dateFormat = (date) => {
  let [month, day, year] = date.split('/'); // 07-14-2021  ///// yyyy-mm-dd

  if (Number(month) > 12) {
    return `${year}-${day}-${month}`;
  }

  return `${year}-${month}-${day}`;
};

function net(amount, perc) {
  return Number((Number(amount) - (Number(amount) * perc) / 100).toFixed(0));
}

export { request, dateFormat, net };
