import React, { Fragment } from 'react';
import 'react-datepicker/dist/react-datepicker.css';
import DatePicker from 'react-datepicker';
import { request, dateFormat } from '../helper';
import '../DARIS.css';
import Button from '@material-ui/core/Button';
import { withRouter } from 'react-router-dom';

class AddEmployee extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      enteredEmployeeId: {},
      employeeDetails: '',
      employeeDesignation: '',
      employeeRole: '',
      locations: [],
      changedLastUpdateDate: new Date(),
      changedUserUpdateDate: new Date(),
      id: props.location.state.id,
    };

    console.log(props.location.state.id);

    this.title = React.createRef('');
    this.firstname = React.createRef('');
    this.lastname = React.createRef('');
    this.employeeId = React.createRef('');
    this.email = React.createRef('');
    this.password = React.createRef('');
    this.mobile = React.createRef('');
    this.gender = React.createRef('');
    this.designation = React.createRef('');
    this.location = React.createRef('');
    this.branchName = React.createRef('');
    this.employeeState = React.createRef('');
    this.region = React.createRef('');
    this.role = React.createRef('');
    this.higherPosition = React.createRef('');

    this.titles = ['Mr', 'Mrs', 'Miss'];
    this.gender = ['Male', 'Female'];
    this.states = ['Andhra Pradesh', 'Kerala', 'Karnataka', 'Telangana', 'Tamilnadu', 'New Delhi', 'Haryana', 'Jammu and Kashmir', 'Uttar Pradesh', 'Punjab', 'Uttarakhand', 'Punjab', 'Uttarakhand', 'Goa', 'Maharashtra', 'Rajasthan', 'Gujarat', 'Bihar', 'Orissa', 'Jharkhand', 'West Bengal', 'Chattisgarh', 'Madhya Pradesh', 'Sikkim', 'Tripura', 'Himachal Pradesh', 'Puducherry', 'Assam', 'Arunachal Pradesh', 'Mizoram', 'Meghalaya', 'Manipur', 'Nagaland'];
    this.regions = ['South', 'East', 'North', 'West', 'Central'];
    this.roles = ['CEO', 'NSM', 'RSM', 'Zonal Manager', 'Branch Manager', 'Assistant Manager', 'Telesales Manager', 'Senior Relationship Manager', 'Relationship Manager', 'Senior Team Lead', 'Team Lead', 'Telecaller'];
  }

  componentDidMount() {
    if (sessionStorage.length === 0) {
      return;
    }
    (async () => {
      const token = sessionStorage.getItem('token');
      let desg = await request('designation/all', token).get();
      let jsonn = await desg.json();

      let bran = await request('branch/all', token).get();
      let resp = await bran.json();

      this.setState({
        designations: jsonn.message,
        branches: resp.message,
      });

      for (const location of resp.message) {
        if (!this.state.locations.includes(location.location)) {
          this.state.locations.push(location.location);
        }
      }
      const res = await request(`employee/select/employeeDetail/${this.state.id}`, token).get();
      const json = await res.json();

      this.setState({
        employeeDetails: json.message[0],
        changedLastUpdateDate: json.message[0]?.last_update_date,
        changedUserUpdateDate: json.message[0]?.user_update_date,
        employeeDesignation: json.message[0].designation,
        employeeRole: json.message[0].role,
      });
    })();
  }

  renderHigherPositionInput = (SelectedRole) => {
    return (
      <Fragment>
        <div className="d-flex row justify-content-between">
          <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
            <label htmlFor="exampleInputPassword1">{SelectedRole} ID</label>
            <input type="text" ref={this.higherPosition} defaultValue={this.state.employeeDetails?.higher_position} name="higher_position" className="form-control" placeholder={`${SelectedRole} ID`} autoComplete="off" />
          </div>
          <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
          <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3"></div>
        </div>
      </Fragment>
    );
  };

  higherPositionFunction = () => {
    switch (this.state.employeeRole) {
      case 'Telecaller':
        return this.renderHigherPositionInput('Team Lead');
      case 'Zonal Manager':
        return this.renderHigherPositionInput('RSM');
      case 'Branch Manager':
        return this.renderHigherPositionInput('Zonal Manager');
      case 'Senior Relationship Manager':
      case 'Telesales Manager':
      case 'Assistant Manager':
        return this.renderHigherPositionInput('Branch Manager');
      case 'Relationship Manager':
        return this.renderHigherPositionInput('Senior Relationship Manager');
      case 'Senior Team Lead':
        return this.renderHigherPositionInput('Telesales Manager');
      case 'Team Lead':
        return this.renderHigherPositionInput('Senior Team Lead');
      default:
        break;
    }
  };

  handleChange = (event) => {
    this.setState({ employeeRole: event.target.value });
  };

  check = () => {
    if (isNaN(Number(this.state.employee_id))) {
      alert('Please Enter valid Employee Id');
      return false;
    } else if (isNaN(Number(this.state.mobile))) {
      alert('Please Enter valid Mobile Number');
      return false;
    }

    return true;
  };

  handleSubmit = async (event) => {
    event.preventDefault();
    const data = {
      employee_id: Number(this.employeeId.current.value),
      title: this.title.current.value,
      firstname: this.firstname.current.value,
      lastname: this.lastname.current.value,
      email: this.email.current.value,
      password: this.password.current.value,
      mobile: Number(this.mobile.current.value),
      gender: this.gender.current.value,
      designation: this.designation.current.value,
      location: this.location.current.value,
      branch: this.branchName.current.value,
      last_update_date: dateFormat(new Date(this.state.changedLastUpdateDate).toLocaleDateString()),
      user_update_date: dateFormat(new Date(this.state.changedUserUpdateDate).toLocaleDateString()),
      state: this.employeeState.current.value,
      region: this.region.current.value,
      role: this.role.current.value,
      higher_position: Number(this.higherPosition.current !== null ? this.higherPosition.current.value : 0),
    };

    const token = sessionStorage.getItem('token');
    let res = await request('employee/update', token).post(data);
    let text = await res.text();
    // let json = await res.json();
    if (res.status === 200) {
      alert('Employee Details Updates Sucessfully');
    }
  };

  onSubmitHandler = async (e) => {
    // e.preventDefault();
    // this.setState({enteredEmployeeId: e.target[0].value})
    // const res = await request(`employee/select/${e.target[0].value}`).get()
    // const json = await res.json();
    // this.setState(
    //     {
    //       employeeDetails : json.message[0],
    //       changedLastUpdateDate : json.message[0]?.last_update_date,
    //       changedUserUpdateDate : json.message[0]?.user_update_date,
    //       employeeDesignation : json.message[0].designation,
    //       employeeRole : json.message[0].role
    //     }
    //     )
  };

  render() {
    return (
      <Fragment>
        <section className="mx-auto col-sm-12 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-6 mb-3 heading p-2">Add Employee Details</h4>
          <div className="headTwo pt-4">
            <form onSubmit={this.handleSubmit}>
              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label>Employee ID</label>
                  <input type="text" placeholder="Employee ID" name="employee_id" autoComplete="off" readOnly ref={this.employeeId} defaultValue={this.state.employeeDetails?.employee_id} className="form-control inputField" required />
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3 d-flex row" style={{ display: 'block' }}>
                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    <label htmlFor="exampleInputPassword1">Title</label>
                    <select required className="form-control inputField" ref={this.title} name="title">
                      Title
                      <option value="">Select Title</option>
                      {this.titles.map((title, index) => {
                        if (title === this.state.employeeDetails?.title) {
                          return (
                            <option key={index} selected value={title}>
                              {title}
                            </option>
                          );
                        }
                        return (
                          <option key={index} value={title}>
                            {title}
                          </option>
                        );
                      })}
                    </select>
                  </div>

                  <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                    <label htmlFor="exampleInputPassword1">First Name</label>
                    <input type="text" ref={this.firstname} defaultValue={this.state.employeeDetails.firstname} name="firstname" required className="form-control inputField" placeholder="First Name" autoComplete="off" />
                  </div>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Last Name</label>
                  <input type="text" ref={this.lastname} defaultValue={this.state.employeeDetails.lastname} name="lastname" required className="form-control inputField" placeholder="Last Name" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputEmail1">Mobile Number</label>
                  <input type="text" ref={this.mobile} defaultValue={this.state.employeeDetails?.mobile} name="mobile" required className="form-control inputFields" aria-describedby="emailHelp" placeholder="Mobile Number" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  -<label htmlFor="exampleInputPassword1">Email</label>
                  <input type="email" ref={this.email} defaultValue={this.state.employeeDetails?.email} name="email" required className="form-control inputField" placeholder="Email" autoComplete="off" />
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Password</label>
                  <input type="password" ref={this.password} defaultValue={this.state.employeeDetails?.password} name="password" required className="form-control inputField" placeholder="Password" autoComplete="off" />
                </div>
              </div>

              <div className="d-flex row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Gender</label>
                  <select required className="form-control inputField" ref={this.gender} name="gender">
                    <option value="">select Gender</option>
                    {this.gender.map((value, index) => {
                      if (value === this.state.employeeDetails?.gender) {
                        return (
                          <option key={index} selected value={value}>
                            {value}
                          </option>
                        );
                      }
                      return (
                        <option key={index} value={value}>
                          {value}
                        </option>
                      );
                    })}
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Last Updated Date</label>
                  <div className="form-control inputField">
                    <DatePicker required className="dateField" placeholderText="Select Date" showPopperArrow={false} selected={new Date(this.state.changedLastUpdateDate ? this.state.changedLastUpdateDate : new Date())} onChange={(date) => this.setState({ changedLastUpdateDate: date })} onKeyDown={(e) => e.preventDefault()} />
                  </div>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">User Updated Date</label>
                  <div className="form-control inputField">
                    <DatePicker required className="dateField" selected={this.state.userdate} placeholderText="Select Date" showPopperArrow={false} selected={new Date(this.state.changedLastUpdateDate ? this.state.changedUserUpdateDate : new Date())} onChange={(date) => this.setState({ changedUserUpdateDate: date })} onKeyDown={(e) => e.preventDefault()} />
                  </div>
                </div>
              </div>

              <div className="d-flex  row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Designation</label>
                  <select required className="form-control inputField" ref={this.designation} name="designation">
                    <option value="">select Designation</option>
                    {this.state.designations?.map((value, index) => {
                      if (value.designation === this.state.employeeDetails?.designation) {
                        return (
                          <option key={index} selected value={value.designation}>
                            {value.designation}
                          </option>
                        );
                      }
                      return (
                        <option key={index} value={value.designation}>
                          {value.designation}
                        </option>
                      );
                    })}
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Location</label>
                  <select required className="form-control inputField" ref={this.location} id="location" name="location">
                    <option value="">select Location</option>
                    {this.state.locations.map((value, index) => {
                      if (value === this.state.employeeDetails?.location) {
                        return (
                          <option key={index} selected value={value}>
                            {value}
                          </option>
                        );
                      }
                      return (
                        <option key={index} value={value}>
                          {value}
                        </option>
                      );
                    })}
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Branch Name</label>
                  <select required className="form-control inputField" ref={this.branchName} name="branch">
                    <option value="">select Branch Name</option>
                    {/* {
                      this.state.branches.map((item, index) => {
                        return (
                          <option key={item.branch + index} value={item.branch}>
                            {item.branch}
                          </option>
                        )
                      })
                    } */}
                    {this.state.branches?.map((value, index) => {
                      if (value.branch === this.state.employeeDetails?.branch) {
                        return (
                          <option key={index} selected value={value.branch}>
                            {value.branch}
                          </option>
                        );
                      }
                      return (
                        <option key={index} value={value.branch}>
                          {value.branch}
                        </option>
                      );
                    })}
                  </select>
                </div>
              </div>

              <div className="d-flex  row justify-content-between">
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">State</label>
                  <select required className="form-control inputField" ref={this.employeeState} id="StateName" name="state">
                    <option value="">Select State</option>
                    {this.states?.map((value, index) => {
                      if (value === this.state.employeeDetails?.state) {
                        return (
                          <option key={index} selected value={value}>
                            {value}
                          </option>
                        );
                      }
                      return (
                        <option key={index} value={value}>
                          {value}
                        </option>
                      );
                    })}
                  </select>
                </div>
                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Region</label>
                  <select required className="form-control inputField" id="roleName" ref={this.region} name="region">
                    <option value="">Select Region</option>
                    {this.regions?.map((value, index) => {
                      if (value === this.state.employeeDetails?.region) {
                        return (
                          <option key={index} selected value={value}>
                            {value}
                          </option>
                        );
                      }
                      return (
                        <option key={index} value={value}>
                          {value}
                        </option>
                      );
                    })}
                  </select>
                </div>

                <div className="col-sm form-group pt-0 pb-0 pr-3 pl-3">
                  <label htmlFor="exampleInputPassword1">Role</label>
                  <select required className="form-control inputField" onChange={(e) => this.handleChange(e)} ref={this.role} id="roleName" name="role">
                    <option value="">Select Role</option>
                    {this.roles?.map((value, index) => {
                      if (value === this.state.employeeDetails?.role) {
                        return (
                          <option key={index} selected value={value}>
                            {value}
                          </option>
                        );
                      }
                      return (
                        <option key={index} value={value}>
                          {value}
                        </option>
                      );
                    })}
                  </select>
                </div>
              </div>
              {this.higherPositionFunction()}
              <div className="d-flex" style={{ width: '100%' }}>
                <button type="submit" className="btn btn-primary d-block btn-sm mx-auto" style={{ borderRadius: '15px', backgroundColor: 'rgb(219, 144, 31)', marginTop: '50px', marginLeft: '15px', color: 'white', border: 'none' }}>
                  Submit
                </button>
              </div>
            </form>
          </div>
        </section>
      </Fragment>
    );
  }
}

export default withRouter(AddEmployee);
