import React, { Fragment } from 'react';
import '../DARIS.css';
import { request, dateFormat } from '../helper';
import Button from '@material-ui/core/Button';
import { withRouter } from 'react-router-dom';
import { css } from '@emotion/core';
import BounceLoader from 'react-spinners/BounceLoader';

class EmployeeDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
    };
  }
  async componentDidMount() {
    if (sessionStorage.length === 0) {
      return;
    }
    const token = sessionStorage.getItem('token');
    let res = await request(`employee/role`, token).get();
    let json = await res.json();
    this.setState({ data: json });
  }

  updateHandler = (e, employeeId) => {
    // console.log('id', employeeId);
    this.props.history.push({
      pathname: '/Home/EmployeeUpdation',
      state: { id: employeeId },
    });
  };

  render() {
    const { data } = this.state;

    return (
      <Fragment>
        {/* <section className="mx-auto col-sm-7 p-3 m-0 rounded">
          <h4 className="text-center mx-auto col-sm-11 mb-3 heading p-2">Search Employee</h4>
          <div className="headTwo pt-4">
            <div className="form-group pt-0 pb-0 pr-3 pl-3" >
              <label htmlFor="exampleInputEmail1" className="mb-0">Employee ID </label>
              <input type="text" className="form-control shadow" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Employee ID" autoComplete="off" />
            </div>
            <div className="d-flex" style={{ width: "100%" }}>
              <input type="submit" className="btn d-block btn-sm mx-auto" style={{ borderRadius: "15px", backgroundColor: "rgb(219, 144, 31)", color: "white" }} />
            </div>
          </div>
        </section> */}

        <div style={{ margin: '10px auto', borderRadius: '10px', border: '1px solid #fff', padding: '5px 30px', boxShadow: ' 0 4px 8px 5px rgba(0,0,0,.2)' }}>
          <h4 style={{ textAlign: 'center' }}>Employee Details</h4>
        </div>
        {data ? (
          <div style={{ overflowX: 'scroll' }}>
            <table className="table table-bordered">
              <thead style={{ backgroundColor: 'rgb(219, 144, 31)', color: 'white', textAlign: 'left' }}>
                <tr>
                  <th scope="col">Employee ID</th>
                  <th scope="col">Name</th>
                  <th scope="col">Mobile Number</th>
                  <th scope="col">Designation</th>
                  <th scope="col">Branch Name</th>
                  <th scope="col">Location</th>
                  <th scope="col">&nbsp;</th>
                  {/* <th scope="col">Edit</th> */}
                </tr>
              </thead>
              <tbody>
                {data !== null
                  ? data.map((dataa, i) => {
                      return (
                        <tr key={i}>
                          <td>
                            <small>{dataa.employee_id}</small>
                          </td>
                          <td>
                            <small>{dataa.firstname}</small>
                          </td>
                          <td>
                            <small>{dataa.mobile}</small>
                          </td>
                          <td>
                            <small>{dataa.designation}</small>
                          </td>
                          <td>
                            <small>{dataa.branch}</small>
                          </td>
                          <td>
                            <small>{dataa.location}</small>
                          </td>
                          {/* <td><Button className="mx-auto" onClick={(e) => this.updateHandler(e, dataa.employee_id)} style={{marginLeft: "60px"}} type="submit" variant="contained" color="primary">Update</Button></td> */}
                          <td>
                            <small>
                              <i onClick={(e) => this.updateHandler(e, dataa.employee_id)} className="far fa-edit"></i>
                            </small>
                          </td>
                        </tr>
                      );
                    })
                  : null}
              </tbody>
            </table>
          </div>
        ) : (
          <div style={{ width: '100%', margin: '50px auto' }}>
            <BounceLoader
              css={css`
                display: block;
                margin: 0 auto;
                border-color: red;
              `}
              size={50}
            />
          </div>
        )}
      </Fragment>
    );
  }
}

export default withRouter(EmployeeDetails);
